.App {
  max-width: 1280px;
  margin: 0 auto;
  text-align: center;
  
}

.globalnavigation{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 15px 0;
  background-color: rgb(249, 204, 238);
  padding: 4%;
  
}

.navpunkt{
  color: black;
  text-decoration: none;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.5em;
}

.billedesection{
  height: 50em;
  max-width: 100%;
  display: flex;
  justify-content: center;
}

.image{
  max-width: 100%;
  height: 80vh;
  position: absolute;align-items: center;
 
}

.textonimg{
 text-transform: uppercase;
 position: relative;
}

.footer{
  height: auto;
  margin-top: 2%;
  background-color: rgb(249, 204, 238);
  padding: 3%;
  position: relative;
 
}

.footerrubrikker{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  line-height: 5pt;
}


.icon{
  color: rgb(18, 94, 8);
  size: 100px 100px;

  
}

/*-------Kort formatering------*/
.kortraekke{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.kort {
  position: relative;
  width: 240px;
  margin: 10px;
  box-shadow: 2px 2px 6px 0px rgba(243, 76, 231, 0.3);
}

.kort .tekst {
  text-align: center;
  padding: 0 10px 50px;
}
/*p:nth-of-type  det betyder at den tekst man tager overfor knappen skal være min i højden 65px høj. 
det er kun relevant hvis man skriver lange linjer i tekst afsnittet.*/
.kort .tekst p:nth-of-type(1) {
  min-height: 65px;
}

.kort h3 {
  text-align: center;
  margin-bottom: 5px;
}


